<template>
	<view>
		<wybar @clickLeft="back" :leftText="className"></wybar>
		<view class="row topSearch">
			<image src="../../../static/search.png" mode="aspectFit"
				style="margin-left: 10px;width: 20px;height: 20px;"></image>
			<input class="sousuokuang" :placeholder="i18n.index.search" type="text" confirm-type="search"
				@confirm="doSearch">
		</view>
		<scroll-view scroll-x="true" style="width: 100%;white-space: nowrap;background-color: #FFF;margin-top: 8px;height: 32px;">
			<text v-for="(item,index) in tabbar" :key="index" :class="index == tabIndex ? 'tab_sel' : 'tab_nor'"
				@click="tabbarClick(index)">
				{{item}}
			</text>
		</scroll-view>
		<storeList :height="scrollHeigth" :typeString="4" @gotoCell="gotoCell"></storeList>

	</view>
</template>

<script>
	import wybar from '../../../components/wybar/wyBar.vue'
	import storeList from '../../../components/storeList/index.vue'

	export default {
		components: {
			wybar,
			storeList
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				listArray: [],
				tabbar: [],
				tabIndex: 0,
				scrollHeigth: 0,
				classID: '',
				className: ''
			}
		},
		onReady() {
			this.tabbar = ['全部', '全部', '全部', '全部', '全部', '全部', '全部', '全部', '全部', '全部']
			const res = uni.getSystemInfoSync();
			var top = 44
			if (parseInt(res.safeArea.top) > top) {
				top = parseInt(res.safeArea.top)
			}
			this.scrollHeigth = getApp().globalData.windowHeight - top - 40 - 46
		},
		onLoad(option) {
			this.classID = option.id
			this.className = option.name
		},
		methods: {
			back() {
				uni.navigateBack({})
			},

			tabbarClick(index) {
				var that = this;
				that.tabIndex = index

			},
			gotoCell(item) {
				uni.navigateTo({
					url: '../store/store'
				})
			},
		}
	}
</script>

<style>
	.topSearch {
		margin-left: 15px;
		margin-top: 10px;
		width: calc(100% - 30px);
		height: 36px;
		border-radius: 8px;
		background-color: #F5F6F7;
		align-items: center;
	}

	.sousuokuang {
		margin-left: 10px;
		margin-right: 10px;
		height: 100%;
		flex: 1;
		font-size: 14px;
		color: #181818;
	}

	.tab_sel {
		font-size: 14px;
		color: #7900B2;
		margin-top: 8px;
		height: 24px;
		line-height: 24px;
		border-radius: 12px;
		background-color: #F5F6F7;
		border: 1px solid #7900B2;
		padding-left: 10px;
		padding-right: 10px;
		margin-left: 15px;
	}


	.tab_nor {
		font-size: 14px;
		color: #7900B2;
		/* margin-top: 8px; */
		height: 24px;
		line-height: 24px;
		border-radius: 12px;
		background-color: #F5F6F7;
		padding-left: 10px;
		padding-right: 10px;
		margin-left: 15px;
	}
</style>
